<template>
  <div class="about">
    <div class="mt-2">
      <apis></apis>
    </div>

    <div class="row justify-content-center text-center">
        <strong class="lead">Demo {{this.$route.params.slug}}</strong>
    </div>


   <!--  <pre>{{this.$route.params.slug}}</pre> -->

   <!-- form reniec -->
  <transition name="slide-fade"> 
    <div class="row justify-content-center" v-if="this.$route.params.slug == 'reniec'">

            <div class="col-12 col-sm-6 text-center">
            <hr>
            <form class="form-inline">
           <div class="col-8">
           <label for="inputPassword2" class="sr-only">Ingresar DNI</label>
           <input type="text" class="form-control" id="inputPassword2" placeholder="12345678" v-model='dni' maxlength="8" @keypress='numeros' @input='digitos'>
           </div>
           <div class="col">
             <button type="button" class="btn btn-info form-control" @click.prevent='reniec'>Consultar</button>
           </div>
           
           </form>
            <hr>
      
            
        </div>
    </div>
</transition>


<!-- form moneda -->
 <transition name="slide-fade">
        <div class="row justify-content-center" v-if="this.$route.params.slug == 'moneda'">

            <div class="col-12 col-sm-6 text-center">
            <hr>
            <form class="form-inline">
           <div class="col">
           <input type="text" class="form-control" id="moneda" placeholder="50.56" v-model='moneda' @keypress='solodecimal' required="">
           </div>
          <!--  <div class="col"></div> -->
           <div class="col">
             <select v-model="tmoneda" class="form-control">
             <option v-for="option in tipomoneda" :key='option.valor' v-bind:value="option.valor">
             {{ option.nombre }}
             </option>
             </select>
           </div>

           <div class="col">
            <div v-if='mmoneda'>
               <button type="button" class="btn btn-info form-control" @click.prevent='limpiarmoneda'>Nuevo</button>
            </div>
            <div v-else>
                
                 <button type="button" class="btn btn-info form-control" @click.prevent='convertirMoneda'>Consultar</button>
            </div>
          
          

           </div>


         

           
           </form>
            <hr>
      
            <div class="row m-2">
      <div class="col-12 alert alert-info" role="alert">
        <strong>Nota!</strong> El limite del Monto  de esta api es 9999 999 999
      </div>
    </div>
        </div>
    </div>
    

</transition>

<!-- form ubigeo -->
  <transition name="slide-fade"> 
    <div class="row justify-content-center" v-if="this.$route.params.slug == 'ubigeo'">

            <div class="col-12 col-sm-6 text-center">
            <hr>
            <div class="alert alert-warning" role="alert">
              <strong>En proceso!</strong> Api de ubigeo estara listo pronto 
            </div>
            <hr>
      
            
        </div>
    </div>
</transition>
    
      <!-- mostrar renie -->
       <transition name="slide-fade">
        <div class="row justify-content-center text-center m-1" v-if="this.$route.params.slug == 'reniec'">
         
         <div class="col-12 col-sm-6 card border-2 border-success " v-if="mdni">

           <div class="text-center m-2"><strong>RESPUESTA DE LA APIREST</strong></div>

            <div class="m-2 text-left">
             
               <div><span><strong>DNI :</strong> {{ddni.dni}}</span></div>
               <div><span><strong>NOMBRES :</strong> {{ddni.nombres}}</span></div>
               <div> <span><strong>APELLIDOS :</strong> {{ddni.apellidos}}</span></div>
            </div>
        </div>
   
        </div>

       </transition>

          <!-- mostrar moneda -->
       <transition name="slide-fade">
        <div class="row justify-content-center text-center m-1" v-if="this.$route.params.slug == 'moneda'">
         
         <div class="col-12 col-sm-6 card border-2 border-success " v-if="mmoneda">

           <div class="text-center m-2"><strong>RESPUESTA DE LA APIREST</strong></div>

            <div class="m-2 text-left">
             
               <div><span><strong>{{moneda}} :</strong> {{dmoneda}}</span></div>
  
            </div>
        </div>
   
        </div>

       </transition>
        
  




  </div>
</template>
<script>
import apis from '@/components/ApisComponent'
import axios from 'axios'


    export default{
        data(){
            return{
                slug:'',
                dni:'',
                ddni:'',
                mdni:false,

                tmoneda:4,
                moneda:'',
                dmoneda:'',
                mmoneda:false,
                show:true,
              
                
                tipomoneda: [
                { nombre: 'DOLARES', valor:  2},
                { nombre: 'SOLES', valor: 4 },
                { nombre: 'EUROS', valor: 3 }
                ]

            }


        },
        components:{
         apis
        },
        mounted(){
          
        },
        updated(){
             this.slug=this.$route.params.slug;
        },
        methods:{

           convertirMoneda:function(){
            var url='http://apis.ccasanisoft.com/api/v1/general/money';
               if(this.moneda !=''){
                 axios.post(url,{
                  amount:this.moneda,
                  currency:this.tmoneda

                })
                .then((res) =>{
                  this.dmoneda=res.data;
                  this.mmoneda=true;

              });
              }else{
                alert("Valor no valido");
              }
           },


            reniec:function(){

           
              var url='http://apis.ccasanisoft.com/api/v1/general/reniec';

              axios.post(url,{
                  dni:this.dni
                })
                .then((res) =>{
                  if(res.data !=0){
                    this.mdni=true
                    this.ddni=res.data
                    this.dni="";
                  }else{
                 this.$alertify.alert('Aviso !','El Nro: '+ this.dni +' es incorrecto | no fue encontrado');
                 this.mdni=false
                 this.dni=""
              }

            });
          },
            numeros:function(e){
              var key = window.event ? e.which : e.keyCode;
              if (key < 48 || key > 57) {
              e.preventDefault();

              }
              
            },
            digitos:function(e){
               if (this.dni.length > 8) 
                e.preventDefault();
              this.dni = this.dni.slice(0,8);

            },
          solodecimal:function(){
/*             e.preventDefault();
*/              this.moneda = this.moneda.replace(/[^0-9,.]/g, '').replace(/,/g, '.');
          },
          limpiarmoneda:function(){
            this.moneda='';
            this.mmoneda=false;
          }



        }
    };
</script>



<style>


.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}

.bounce-enter-active {
  animation: bounce-in .5s;
}
.bounce-leave-active {
  animation: bounce-in .5s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}
</style>